import Vue from 'vue';
import Router from 'vue-router';
import Home from './components/Home.vue';
import Register from './components/Register.vue';
import Login from './components/Login.vue';
import NutritionalConstraints from './components/NutritionalConstraints.vue';
import MinimizeCost from './components/MinimizeCost.vue';
import MaximizeProfit1 from './components/MaximizeProfit1.vue';
import MaximizeProfit2 from './components/MaximizeProfit2.vue';

// IF THE APP SI SLOW, TRY TO LOAD COMPONENTS ASYNC
// SI L'APP EST LENTE , CHARGER LES COMPONENTS DE FACON ASYNCHRONE.
const AsyncPortfolioOptimization = () => import ("./components/PortfolioOptimization.vue");

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/MinimizeCost',
      name: 'MinimizeCost',
      component: MinimizeCost
    },
    {
      path: '/maximizeProfit1',
      name: 'MaximizeProfit1',
      component: MaximizeProfit1
    },
    {
      path: '/maximizeProfit2',
      name: 'MaximizeProfit2',
      component: MaximizeProfit2
    },
    {
      path: '/nutritionalConstraints',
      name: 'NutritionalConstraints',
      component: NutritionalConstraints
    },
    {
      path: '/portfolioOptimization',
      name: 'PortfolioOptimization',
      component: AsyncPortfolioOptimization
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ],
});
