<template>
<div class="main-form">

    <div class="row">

        <!-- SIDE  -->
        <div class="col-md-2">
            <img src="../assets/bourse.jpg" class="main_img_big">
            <h5> {{ t('LOGIN') }}</h5>
            <h5> </h5>
        </div>

        <!-- MAIN  -->
        <div class="col-md-10">

<form class="user" id="form">
  <div class="container">
    <h1>{{ t('LOGIN') }}</h1>
    <p>Connect your account</p>
    <hr>

    <label for="email"><b>{{ t('EMAIL') }}</b></label>
    <input type="email" class="mr-sm-2 form-control form-control-sm" min="1" v-model="user.email" name="email" id="email" placeholder="Email">
    <!-- ERRORS MESSAGES -->
    <div v-if="cliqued ">
        <div class="error" v-if="!$v.user.email.required">
            Field is mandatory
        </div>
        <div class="error" v-if="!$v.user.email.minLength">
            Email must be at least
            {{ $v.user.email.$params.minLength.min }} long.
        </div>
        <div class="error" v-if="!$v.user.email.email">
            Email must be valid
        </div>
    </div>

    <label for="password"><b>{{ t('PASSWORD') }}</b></label>
    <input type="password" class="mr-sm-2 form-control form-control-sm" min="1" v-model="user.password" name="password" id="password"  placeholder="Password">
    <!-- ERRORS MESSAGES -->
        <div v-if="cliqued ">
        <div class="error" v-if="!$v.user.password.required">
            Field is necessary
        </div>
        <div class="error" v-if="!$v.user.password.minLength">
            Password should be at least
            {{ $v.user.password.$params.minLength.min }} long.
        </div>
    </div>

    <hr>

    <button type="button" class="btn my-2 my-sm-0 btn-secondary btn-sm" v-on:click='login'>{{ t('LOGIN') }}</button>&nbsp;
    <button type="button" class="btn my-2 my-sm-0 btn-secondary btn-sm" v-on:click='resetlogin'>{{ t('RESET') }}</button>
  </div>
  
  <div class="container signin">
    <p>{{ t('NEW_THERE') }} <router-link class-active="active" to="/register">{{ t('REGISTER') }}</router-link>.</p>
  </div>
</form>
        </div>
    </div>
</div>
</template>
<script>
// Importing libs
import axios from 'axios';
import {
    required,
    minLength,
    between, 
    email 
} from "vuelidate/lib/validators";

// Shared functions
import shared from '../shared'

export default {
    data() {
        return {
            user: {
                email: "",
                password: ""
            },
            problem: false,
            cliqued:false
        }
    },
    validations: {
        user: {
            email: {
                required,
                minLength: minLength(2),
                email
            },
            password: {
                required,
                minLength: minLength(2)
            }
        }
    },
    methods: {
        resetlogin: function() {
            this.user = {
                email: "",
                password: ""
            };
            this.problem=false;
            this.cliqued=false;
        },
        login: function () {
            this.cliqued = true;  
            if (
                !this.user.password ||
                !this.user.email||
                !this.$v.user.email.email
            ) {
                this.problem = true;
                this.$notify({
                    type: 'error',
                    group: 'foo',
                    title: 'Hey! ',
                    text: 'Something is wrong, please check the fields again !'
                });
                return;
            }
            // Call server
            axios
                .post("/loginUser", this.user)
                .then(response => {
                    if (response.data.problem) {
                        alert(response.data.problem);
                        console.log(response.data.problem);
                    } else {
                        this.$notify({
                            type: 'success',
                            group: 'foo',
                            title: 'Hey! ',
                            text: 'Authorise user !'
                        });
                    }
                })
                .catch(error => {
                    console.log(error);
                    this.$notify({
                        type: 'error',
                        group: 'foo',
                        title: 'Hey! ',
                        text: error
                    });
                });
        }
    }
}
</script>
<style>
.container { padding: 16px; background-color: #FEF5E4;  }
hr { border: 1px solid grey;  margin-bottom: 25px; }
a { color: dodgerblue; }
.signin { background-color: #f1f1f1; text-align: center; }
.error { color: #FF0000; }
</style>