<template>
<div class="main-form">

    <div class="row">

        <!-- SIDE  -->
        <div class="col-md-2">
            <img src="../assets/pate.jpeg" class="main_img_big">
            <h5> Improve 100g Food Quality </h5>
            <h5> Améliorer la qualité d'un paté de 100g </h5>
            <h5 style="color:red"> BUILDING PLEAZ WAIT </h5>
        </div>

        <!-- MAIN  -->
        <div class="col-md-10">
            <b-form @submit="onSubmit" v-if="show">
                <b-tabs active-nav-item-class="font-weight-bold  text-danger" >
                    <b-tab active>
                          <template v-slot:title>
                             <b-icon icon="gear-fill" aria-hidden="true"></b-icon> L.P </strong>description
                        </template>
                        <b-form-input v-model="lpDescription"></b-form-input><br>

                        <fieldset>
                            <legend> English </legend>

                            <b>Solving a Linear Programming problem with Python (Pulp)</b><br><br>
                            Posted on November 28, 2012 by Thomas Cokelaer <br> <br>

                            <a href="https://coin-or.github.io/pulp/CaseStudies/a_blending_problem.html">
                                Watch the problem on the Pulp website</a>

                        </fieldset>
                        <fieldset>
                            <legend> French</legend>
                            # Création de paté sous contrainte nutritionelles .<br><br>

                            # Elément Proteines Gras Fibre Sel<br>
                            # Poulet 0.100 0.080 0.001 0.002<br>
                            # Steak 0.200 0.100 0.005 0.005<br><br>

                            # Le prix du poulet est de 0.013 euros par gramme<br>
                            # Le prix du steak est de 0.008 euros par gramme<br>
                        </fieldset>

                    </b-tab>

                    <b-tab>
                        <template v-slot:title>
                              <b-icon icon="card-list" aria-hidden="true"></b-icon> My <strong>Items</strong>
                        </template>
                        <b-card-group deck>
                            <b-card class="b-card-perso mw-25" header-tag="header" footer-tag="footer" img-alt="Image" v-for="item in items" img-top :key="item.name">

                                <img :src="require(`../assets/${item.img}`)" v-bind:alt="img" alt="Image" class="card-img-top card-img-spe">

                                <template v-slot:header>
                                    <h6 class="mb-0">{{item.name | capitalize}}(g/g)</h6>
                                </template>
                                <br><br>
                                <b-input-group prepend="Cost" append="(cents/g) $" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input @keypress="isNumber($event)" @change="item.cost = parseFloat(item.cost).toFixed(3) " class="float-right" v-model="item.cost"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.cost" :min="0" :max="0.1" :interval="0.001"></vue-slider><br>

                                <b-input-group prepend="Proteins" append="(g/g) " class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input @keypress="isNumber($event)" @change="item.proteins = parseFloat(item.proteins).toFixed(3) " class="float-right" v-model="item.proteins"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.proteins" :min="0" :max="1" :interval="0.001"></vue-slider><br>

                                <b-input-group prepend="Fats" append="(g/g) " class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input @keypress="isNumber($event)" @change="item.fats = parseFloat(item.fats).toFixed(3) " class="float-right" v-model="item.fats"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.fats" :min="0" :max="1" :interval="0.001"></vue-slider><br>

                                <b-input-group prepend="Fibers" append="(g/g) " class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input @keypress="isNumber($event)" @change="item.fibers = parseFloat(item.fibers).toFixed(3) " class="float-right" v-model="item.fibers"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.fibers" :min="0" :max="1" :interval="0.001"></vue-slider><br>

                                <b-input-group prepend="Salt" append="(g/g) " class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input @keypress="isNumber($event)" @change="item.salt = parseFloat(item.salt).toFixed(3) " class="float-right" v-model="item.salt"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.salt" :min="0" :max="1" :interval="0.001"></vue-slider><br>
                            </b-card>

                        </b-card-group>

                    </b-tab>
                    <b-tab>
                        <template v-slot:title>
                              <b-icon icon="hammer" aria-hidden="true"></b-icon> My <strong>Constraints</strong>
                        </template>
                        <b-card-group deck>
                            <b-card class="b-card-perso mw-25" v-for="constraint in constraints" header-tag="header" footer-tag="footer" img-alt="Image" img-top :key="constraint.name">
                                <img :src="require(`../assets/${constraint.img}`)" alt="Image" class="card-img-top card-img-spe">
                                <template v-slot:header>
                                    <h6 class="mbs-0">{{constraint.name | capitalize }}</h6>
                                </template>
                                <br><br>
                                <b-card-text>
                                    <b-input-group :prepend="constraint.comparator" append="G" class="mb-2 mr-sm-2 mb-sm-0">
                                        <b-form-input @keypress="isNumber($event)" @change="constraint.value = parseFloat(constraint.value).toFixed(2) " class="float-right" v-model="constraint.value"> (g)</b-form-input>
                                    </b-input-group>
                                </b-card-text><br>
                                <!-- <range-slider class="slider" min="0" max="100" step="0.1" v-model="constraint.value"> </range-slider> -->
                                <vue-slider v-model="constraint.value" :min="0" :max="100" :interval="0.1"></vue-slider>
                                <template v-slot:footer>
                                    <em class="float-right">Parameter me !</em>
                                </template>
                            </b-card>
                        </b-card-group>

                    </b-tab>

                    <b-tab >
                         <template v-slot:title>
                            <b-spinner type="grow" small></b-spinner> My Solver <strong> result</strong>
                        </template>
                        
                            <div class="row">
                                <div class="col-lg-4">

                                    <b-card header="Reminder - Items and constraints :" class ="reminder">
                                        Items :
                                        <pre class="m-0">{{ items }} </pre>
                                        Constraints :
                                        <pre class="m-0"> {{ constraints }}</pre>
                                    </b-card>

                                </div>
                                <div class="col-lg-6">
                                    <b-card header="Linear solver result">
                                        <pre class="m-0">{{ linearSolverResult }}</pre>
                                    </b-card>
                                    <b-card header="Pulp L.p description">

                                        <pre class="m-0">{{ PulpLpSummary }}</pre>

                                    </b-card>
                                </div>
                                <div class="col-lg-2">
                                    <b-button class="float-right" type="submit" variant="primary">Calculate solution with PULP</b-button>
                                </div>
                            </div>
                    </b-tab>
                </b-tabs>
            </b-form>
        </div>
    </div>
</div>
</template>

<script>
// Importing libs
import axios from 'axios';

import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'

// https://github.com/ktsn/vue-range-slider
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

// Shared functions
import shared from '../shared'

export default {
    data() {
        return {
            items: [{
                "name": "chicken",
                "cost": 0.013,
                "proteins": 0.100,
                "fats": 0.080,
                "fibers": 0.001,
                "salt": 0.002,
                "img": "280px-Max's_Roasted_Chicken_-_Evan_Swigart.jpg"
            }, {
                "name": "meat",
                "cost": 0.008,
                "proteins": 0.100,
                "fats": 0.100,
                "fibers": 0.005,
                "salt": 0.005,
                "img": "meat.jpg"
            }],
            constraints: [

                {
                    "name": "proteins",
                    "value": 8.0,
                    "img": "proteins.jpg",
                    "comparator": ">="
                },
                {
                    "name": "fats",
                    "value": 6.0,
                    "img": "Saturated-Fat-The-Paleo-Mom-800x600.jpg",
                    "comparator": ">="
                },
                {
                    "name": "fibers",
                    "value": 2.0,
                    "img": "Broccoli__carrots__asparagus.jpg",
                    "comparator": "<="
                },
                {
                    "name": "salt",
                    "value": 0.4,
                    "img": "qrehy0ot93b21.jpg",
                    "comparator": "<="
                }
            ],
            show: true,

            linearSolverResult: {},
            lpDescription: "Set up your 100 grammes box of pâté under nutritional constraints !",
            PulpLpSummary: "",
            img: ""
        }
    },
    components: {
        VueSlider,
        RangeSlider
    },
    computed: {
        total: function () {
            return this.value * 10
        }
    },
    methods: {
        async test() {
            // Just an async function, for FUN 
            return "test"
        },
        getLinearSolver() {
            // DEEP CLONING FRONT END DATA
            var data = {
                "items": JSON.parse(JSON.stringify(this.items)), // WE need to do a deep clone otherwize it crashes vue.js 
                "constraints": JSON.parse(JSON.stringify(this.constraints)) // WE need to do a deep clone otherwize it crashes vue.js 
            }
            this.formatDataForPulp(data).then((res) => {

                const path = '/api/nutritional_constraints_1';
                axios.post(path, res)
                    .then((res) => {
                        this.linearSolverResult = res.data;

                        // Designing the PULP Lp desc coming from pulp
                        this.PulpLpSummary = this.linearSolverResult.PulpLpSummary;
                        this.PulpLpSummary.replace(new RegExp('\r?\n', 'g'), '<br />');

                        // Removing the PulpLpSUmmary from the main JSON result
                        delete this.linearSolverResult.PulpLpSummary;
                    })
                    .catch((error) => {
                        // eslint-disable-next-line
                        console.error(error);
                    });

            })
        },
        isNumber: function (evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        onSubmit(evt) {
            evt.preventDefault()
            this.getLinearSolver()
        },
        reset() {

            location.reload()
            // evt.preventDefault()
            // Reset our form values TODO

            // Trick to reset/clear native browser form validation state
            this.show = false
            this.$nextTick(() => {
                this.show = true
            })
        }
    },
    filters: {
        capitalize: function (value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    created() {
        // Getting the shared function 
        this.formatDataForPulp = shared.formatDataForPulp.bind(this) // now you can call this.foo() (in your functions/template)
    }
}
</script>

<style>
.main_form {
    margin: 30px;
}

.main_img {
    width: 100px;
    height: 100px;
}

.main_img_big {
    width: 150px;
    height: 150px;
}

fieldset {
    background-color: #ffe6ba !important;
    border-radius: 4px;
    padding: 10px;
}

