    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    color: var(--grey) !important;
    font-size: 17px;
    font-weight: bold;
    padding: 3px 5px 3px 7px;
    width: auto;
} */
</style>
