<template>
<div class="main-form">
    <div class="row">
        <!-- SIDE  -->
        <div class="col-md-2">
            <img src="../assets/industry-1430036-1207834.webp" class="main_img">
            <h5> {{ t('DROPDOWN_TITLE_1') }}</h5>
            <span v-if="PulpLpSummary">
                <h3>
                    <b-badge variant="danger">Your Profit : {{linearSolverResult.TotalProfit}} $</b-badge>
                    <div v-for="index,key in linearSolverResult">
                        <b-badge v-if="key.includes('x')" variant="warning">Please Build : {{index}} {{key}} </b-badge>
                    </div>
                </h3>
            </span>
        </div>
        <!-- MAIN  -->
        <div class="col-md-10">
            <b-form @submit="onSubmit" @reset="onReset" v-if="show">
                <b-tabs active-nav-item-class="font-weight-bold  text-danger">
                    <b-tab active>
                        <template v-slot:title>
                            <b-icon icon="gear-fill" aria-hidden="true"></b-icon> {{ t('LP_DESC') }}
                        </template>
                        <b-form-input v-model="lpDescription"></b-form-input><br>
                        <p>
                        </p>
                        <fieldset>
                            <legend> English</legend>
                            <b>Solving a Linear Programming problem with Python (Pulp)</b><br><br>
                            Posted on November 28, 2012 by Thomas Cokelaer <br> <br>
                            <a href=" https://thomas-cokelaer.info/blog/2012/11/solving-a-linear-programming-problem-with-python-pulp/"> Watch the problem on the
                                Thomas Cokelaer's blog </a><br><br>
                            Linear Programming is a type of optimisation where an objective function should be maximised given some constraints.<br> Let us consider the following simple problem (from The GNU Linear Programming Kit, Part 1). Let us say that you want to maximize profits by selling wood soldiers (denoted x1) and wood trains (denoted x2) given that the margin is 3$ for one soldier and 2$ for one train, you want to maximize the profit. In addition, you have the following constraints per week:
                            a soldier requires 2 hours of finishing labour. a train requires 1 hour of finishing labour. <br>You have only 100 hours of finishing labour avaiable weekly
                            a soldier requires 1 hour of carprentry labour. <br>Same for a train. You have only 80 hours of carpentry labour available weekly
                            demand for soldiers not more than 40 per week<br><br>
                            The constraints can be transformed into equations:<br>
                            2*x1 + x2 <= 100<br>
                                x1 + x2 <= 80<br>
                                    x1 <=40<br>
                                        and of course, x1>=0 and x2=0 otherwise there is nothing to optimise.<br>
                        </fieldset>
                        <fieldset>
                            <legend> French</legend>
                            <b>Résolution d'un problème de programmation linéaire avec Python (Pulp)</b><br><br>
                            Publié le 28 novembre 2012 par Thomas Cokelaer<br> <br>
                            La programmation linéaire est un type d'optimisation où une fonction objectif doit être maximisée compte tenu de certaines contraintes. Considérons le problème simple suivant (du kit de programmation linéaire GNU, partie 1). Supposons que vous souhaitiez maximiser les profits en vendant des soldats en bois (notés x1) et des trains en bois (notés x2) étant donné que la marge est de 3 $ pour un soldat et de 2 $ pour un train, vous voulez maximiser le profit. <br>De plus, vous avez les contraintes suivantes par semaine:
                            un soldat a besoin de 2 heures de travail de finition. un train nécessite 1 heure de travail de finition. <br>Vous n'avez que 100 heures de travail de finition disponibles chaque semaine
                            un soldat a besoin d'une heure de travail de charpentier. Idem pour un train. <br>Vous n'avez que 80 heures de travail de menuiserie disponibles par semaine
                            demande de soldats pas plus de 40 par semaine<br><br>
                            Les contraintes peuvent être transformées en équations:<br>
                            2 * x1 + x2 <= 100<br>
                                x1 + x2 <= 80<br>
                                    x1 <= 40<br>
                                        et bien sûr, x1> = 0 et x2 = 0 sinon il n'y a rien à optimiser.
                        </fieldset>
                    </b-tab>
                    <b-tab>
                        <template v-slot:title>
                            <b-icon icon="gear-fill" aria-hidden="true"></b-icon> {{ t('MY_ITEMS') }}
                        </template>
                        <b-nav pills>
                            <b-nav-item-dropdown id="my-nav-dropdown"  toggle-class="nav-link-custom" left>
                                 <template slot="button-content"><b>{{ t('MENU') }} </b></template>
                                <b-dropdown-item @click="createItem">{{ t('ADD_ITEM') }}</b-dropdown-item>
                                <!-- <b-dropdown-divider></b-dropdown-divider> -->
                            </b-nav-item-dropdown>
                        </b-nav>
                        <b-card-group deck>
                            <b-card class="b-card-perso mw-25" header-tag="header" footer-tag="footer" img-alt="Image" v-for="(item, index) in items" img-top :key="item.name">
                                <img :src="require(`../assets/${item.img}`)" v-bind:alt="img" alt="Image" class="card-img-top card-img-spe">
                                <template v-slot:header>
                                    <h6 class="mb-0">{{item.name | capitalize }} <span class="float-right"> X{{index + 1}}</span></h6>
                                </template>
                                <br>
                                <!-- <b-card-text>
                                    <b-badge variant="danger">Margin (Objective)</b-badge><span class="float-right"> {{item.margin}} $</span>
                                </b-card-text>
                                <range-slider class="slider" min="0" max="100" step="1" v-model="item.margin"> </range-slider> -->
                                <b-input-group prepend="Margin (Objective)" append="$" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="item.margin = parseFloat(item.margin).toFixed(1) " class="float-right" v-model="item.margin"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.margin" :min="0" :max="100" :interval="0.1"></vue-slider>
                                <b-input-group prepend="Labour time" append="Hours" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="item.labour = parseFloat(item.labour).toFixed(1) " class="float-right" v-model="item.labour"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.labour" :min="0" :max="100" :interval="1"></vue-slider>
                                <b-input-group prepend="Finition time" append="Hours" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="item.finition = parseFloat(item.finition).toFixed(1) " class="float-right" v-model="item.finition"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.finition" :min="0" :max="100" :interval="1"></vue-slider>
                                <b-input-group prepend="Max Commercial demand" append="Units" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="item.finition = parseFloat(item.demandMax).toFixed(1) " class="float-right" v-model="item.demandMax"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.demandMax" :min="0" :max="1000" :interval="1"></vue-slider>
                                <b-input-group prepend="Min Commercial demand" append="Units" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="parseFloat($event)" @change="item.finition = parseFloat(item.demandMin).toFixed(1) " class="float-right" v-model="item.demandMin"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="item.demandMin" :min="0" :max="1000" :interval="1"></vue-slider>
                                <template v-slot:footer>
                                    <b-button class="float-right" @click="deleteItem(index)" variant="danger">X</b-button>
                                </template>
                            </b-card>
                            <b-card v-if="showNewItem" class="b-card-perso mw-25" header-tag="header" footer-tag="footer" img-alt="Image">
                                <!-- <img :src="require(`../assets/${item.img}`)" v-bind:alt="img" alt="Image" class="card-img-top card-img-spe"> -->
                                <img :src="require(`../assets/${newItem.img}`)" v-bind:alt="img" alt="Image" class="card-img-top card-img-spe">
                                <template v-slot:header>
                                    <b-input-group prepend="Enter name">
                                        <b-form-input v-model="newItem.name" class="mb-0"></b-form-input>
                                    </b-input-group>
                                </template>
                                <br>
                                <b-input-group prepend="Margin (Objective)" append="$" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="newItem.margin = parseFloat(newItem.margin).toFixed(1) " class="float-right" v-model="newItem.margin"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="newItem.margin" :min="0" :max="100" :interval="0.1"></vue-slider>
                                <b-input-group prepend="Labour time" append="Hours" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="newItem.labour = parseFloat(newItem.labour).toFixed(1) " class="float-right" v-model="newItem.labour"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="newItem.labour" :min="0" :max="100" :interval="1"></vue-slider>
                                <b-input-group prepend="Finition time" append="Hours" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="newItem.finition = parseFloat(newItem.finition).toFixed(1) " class="float-right" v-model="newItem.finition"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="newItem.finition" :min="0" :max="100" :interval="1"></vue-slider>
                                <b-input-group prepend="Max Commercial demand" append="Units" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="newItem.finition = parseFloat(newItem.demandMax).toFixed(1) " class="float-right" v-model="newItem.demandMax"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="newItem.demandMax" :min="0" :max="1000" :interval="1"></vue-slider>
                                <b-input-group prepend="Min Commercial demand" append="Units" class="mb-2 mr-sm-2 mb-sm-0">
                                    <b-form-input :number="true" @keypress="isNumber($event)" @change="newItem.finition = parseFloat(newItem.demandMin).toFixed(1) " class="float-right" v-model="newItem.demandMin"> </b-form-input>
                                </b-input-group>
                                <vue-slider v-model="newItem.demandMin" :min="0" :max="1000" :interval="1"></vue-slider>
                                <template v-slot:footer>
                                    <b-button class="float-right" @click="insertItem()" variant="primary">Save</b-button>
                                </template>
                            </b-card>
                        </b-card-group>
                    </b-tab>
                    <b-tab>
                        <template v-slot:title>
                            <b-icon icon="gear-fill" aria-hidden="true"></b-icon> {{ t('MY_CONSTRAINTS') }}
                        </template>
                        <b-card-group deck>
                            <b-card class="b-card-perso mw-25" v-for="constraint in constraints" header-tag="header" footer-tag="footer" img-alt="Image" img-top :key="constraint.name">
                                <img :src="require(`../assets/${constraint.img}`)" alt="Image" class="card-img-top card-img-spe">
                                <template v-slot:header>
                                    <h6 class="mbs-0">{{constraint.name | capitalize }}</h6>
                                </template>
                                <br>
                                <b-card-text>Available work force per week <span class="float-right"> {{constraint.value}} Hours</span></b-card-text>
                                <range-slider class="slider" min="0" max="1000" step="10" v-model="constraint.value"> </range-slider>
                                <template v-slot:footer>
                                    <em class="float-right">Parameter me !</em>
                                </template>
                            </b-card>
                        </b-card-group>
                    </b-tab>
                    <b-tab>
                        <template v-slot:title>
                            <b-icon icon="gear-fill" aria-hidden="true"></b-icon> {{ t('MY_SOLVER_RESULT') }}
                        </template>
                        <div class="row">
                            <div class="col-lg-4">
                                <b-card header="Reminder - Items and constraints :" class="reminder">
                                    Items :
                                    <pre class="m-0">{{ items }} </pre>
                                    Constraints :
                                    <pre class="m-0"> {{ constraints }}</pre>
                                </b-card>
                            </div>
                            <div class="col-lg-6">
                                <b-card header="Linear solver result">
                                    <pre class="m-0">{{ linearSolverResult }}</pre>
                                </b-card>
                                <b-card header="Pulp L.p description">
                                    <pre class="m-0">{{ PulpLpSummary }}</pre>
                                </b-card>
                            </div>
                            <div class="col-lg-2">
                                <b-button class="float-right" type="submit" variant="primary">{{ t('CALCULATE_SOLUTION_PULP') }}</b-button>
                            </div>
                        </div>
                    </b-tab>
                </b-tabs>
            </b-form>
        </div>
    </div>
</div>
</template>
<script>
// Importing libs
import axios from 'axios';
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'
// https://github.com/ktsn/vue-range-slider
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'
// Shared functions
import shared from '../shared'
export default {
    data() {
        return {
            // DEMO DATA MODEL : TIEMS AND CONSTRAINTS
            items: [{
                    "name": "soldat",
                    "margin": 3,
                    "labour": 2,
                    "finition": 1,
                    "demandMax": 40,
                    "demandMin": 0,
                    "img": "wood-soldats.jpg"
                },
                {
                    "name": "train",
                    "margin": 2,
                    "labour": 1,
                    "finition": 1,
                    "demandMax": 800,
                    "demandMin": 0,
                    "img": "wood-train.jpg"
                }
            ],
            constraints: [{
                    "name": "labour",
                    "value": 100,
                    "img": "carpentry.jpg"
                },
                {
                    "name": "finition",
                    "value": 80,
                    "img": "finition.jpg"
                }
            ],
            // WHEN YOU ADD A NEW ITEM 
            newItem: {
                "name": "New Item",
                "margin": 1,
                "labour": 1,
                "finition": 1,
                "demandMax": 50,
                "demandMin": 1,
                "img": "new.jpg"
            },
            img: "",
            show: true,
            linearSolverResult: {},
            PulpLpSummary: "",
            lpDescription: "Maximize my weekly factory profit under available hours of work constraints !",
            showNewItem: false
        }
    },
    components: {
        VueSlider,
        RangeSlider
    },
    computed: {
        total: function () {
            return this.value * 10
        }
    },
    methods: {
        createItem() {
            this.showNewItem = true;
        },
        insertItem() {
            if (this.ItemNameDuplicate(this.newItem)) {
                alert("Please change the name, there is a duplicate")
            } else {
                this.items.push(JSON.parse(JSON.stringify(this.newItem))); // UNBINDING 
                this.showNewItem = false;
            }
        },
        deleteItem(index) {
            this.items.splice(index, 1)
        },
        ItemNameDuplicate(item) {
            var duplicate = false;
            this.items.forEach(function (object) {
                if (object.name.valueOf() == item.name.valueOf()) {
                    console.log('identique')
                    duplicate = true
                }
            });
            if (duplicate) {
                return true
            } else {
                return false
            }
        },
        isNumber: function (evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        getLinearSolver() {
            const path = '/api/maximize_profit_1';
            var data = {
                "items": JSON.parse(JSON.stringify(this.items)), // WE need to do a deep clone otherwize it crashes vue.js 
                "constraints": JSON.parse(JSON.stringify(this.constraints)) // WE need to do a deep clone otherwize it crashes vue.js 
            }
            // CALLING ASYNC formatDataForPulp FUNCTION
            this.formatDataForPulp(data).then((res) => {
                // CALLING AXIOS FUNCTION
                axios.post(path, res)
                    .then((res) => {
                        this.linearSolverResult = res.data;
                        // Designing the PULP Lp desc coming from pulp
                        this.PulpLpSummary = this.linearSolverResult.PulpLpSummary;
                        this.PulpLpSummary.replace(new RegExp('\r?\n', 'g'), '<br />');
                        // Removing the PulpLpSUmmary from the main JSON result
                        delete this.linearSolverResult.PulpLpSummary;
                    })
                    .catch((error) => {
                        // eslint-disable-next-line
                        console.error(error);
                    });
            })
        },
        onSubmit(evt) {
            evt.preventDefault()
            this.getLinearSolver()
        },
        onReset(evt) {
            evt.preventDefault()
            // Reset our form values
            // TODO
            // this.form.email = ''
            // this.form.name = ''
            // this.form.food = null
            // this.form.checked = []
            // Trick to reset/clear native browser form validation state
            this.show = false
            this.$nextTick(() => {
                this.show = true
            })
        }
    },
    filters: {
        capitalize: function (value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    },
    created() {
        // Getting the shared function 
        this.formatDataForPulp = shared.formatDataForPulp.bind(this) // now you can call this.foo() (in your functions/template)
    }
}
</script>
<style>
.main_img {
    width: 150px
}
fieldset {
    background-color: #fef5e4 !important;
    border-radius: 4px;
}
legend {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    color: var(--purple);
    font-size: 17px;
    font-weight: bold;
    padding: 3px 5px 3px 7px;
    width: auto;
}
.slider {
    /* overwrite slider styles */
    width: 100%;
}
</style>
