                                    <tr>
                                        <td>Ressource C</td>
                                        <td>1</td>
                                        <td>2</td>
                                        <td>3</td>
                                        <td>3</td>
                                        <td>24</td>
                                    </tr>
                                    <tr>
                                        <td>Profit</td>
                                        <td>7</td>
                                        <td>9</td>
                                        <td>18</td>
                                        <td>17</td>
                                        <td></td>
                                    </tr>
                                </tbody>
                            </table>
                            Here is the PL:<br>
                            Maximize<br>
                            z = 7x1 + 9x2 + 18x3 + 17x4<br>
                            s. vs.<br>
                            2x1 + 4x2 + 5x3 + 7x4≤42<br>
                            x1 + x2 + 2x3 + 2x4≤17<br>
                            x1 + 2x2 + 3x3 + 3x4≤24<br>
                            x1, x2, x3, x4≥0<br><br>
                            NOTE: The Pl is not put too well in dictionary - list format, you have to arrange (factorize)<br>
                            this so that if the data comes from a database, everything is done automatically (from n and no fixed values).<br>
                            The result is ok and conforms to the handout from the University of Rennes.<br>

                        </fieldset>
                        <fieldset>
                            <legend> French</legend>

                            <b>Usine de meubles</b> <br><br>
                            Origine du problème entier: Université de Rennes 1 et INRIA Rennes Bretagne-Atlantique <br><br>
                            Une entreprise fabrique quatre produits qui lui rapportent respectivement 7,9,18 et 17 euros de bénéfices. <br>
                            Pour ce faire, elle utilise les trois ressources A, B, C dont elle dispose en stock 42,17,24 unités. <br>
                            Les ressources consommées pour fabriquer une unité de chacun de ces produits sont indiquées dans le tableau ci-dessous. <br>
                            L'entreprise souhaite maximiser ses bénéfices. <br><br>
                            Soit xi la quantité de produit désirée. <br>

                            <table class="table table-responsive">
                                <thead>
                                    <tr>
                                        <th class=""></th>
                                        <th>produit 1</th>
                                        <th>produit 2</th>
                                        <th>produit 3</th>
                                        <th>produit 4</th>
                                        <th>stock</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>Ressource A</td>
                                        <td>2</td>
                                        <td>4</td>
                                        <td>5</td>
                                        <td>7</td>
                                        <td>42</td>
                                    </tr>
                                    <tr>
                                        <td>Ressource B</td>
                                        <td>1</td>
                                        <td>1</td>
                                        <td>2</td>
                                        <td>2</td>
                                        <td>17</td>
                                    </tr>
                                    <tr>
                                        <td>Ressource C</td>
                                        <td>1</td>
                                        <td>2</td>
                                        <td>3</td>
                                        <td>3</td>
                                        <td>24</td>
                                    </tr>
                                    <tr>
                                        <td>Profit</td>
                                        <td>7</td>
                                        <td>9</td>
                                        <td>18</td>
                                        <td>17</td>
                                        <td></td>
                                    </tr>
                                </tbody>
                            </table>
                            Voici le PL: <br><br>
                            Maximiser <br>
                            z = 7x1 + 9x2 + 18x3 + 17x4 <br>
                            s. vs <br>
                            2x1 + 4x2 + 5x3 + 7x4≤42 <br>
                            x1 + x2 + 2x3 + 2x4≤17 <br>
                            x1 + 2x2 + 3x3 + 3x4≤24 <br>
                            x1, x2, x3, x4≥0 <br><br>
                            NOTE: Le Pl n'est pas trop bien mis dans le format dictionnaire - liste, il faut organiser (factoriser) <br>
                            ceci pour que si les données proviennent d'une base de données, tout se fait automatiquement (à partir de n et pas de valeurs fixes). <br>
                            Le résultat est correct et conforme au document de l'Université de Rennes. <br>

                        </fieldset>

                    </b-tab>

                    <b-tab title="My Items">
                        <b-card-group deck>
                            <b-card class="b-card-perso mw-25" header-tag="header" footer-tag="footer" img-alt="Image" v-for="item in items" img-top :key="item.name">

                                <img :src="require(`../assets/${item.img}`)" v-bind:alt="img" alt="Image" class="card-img-top card-img-spe">

                                <template v-slot:header>
                                    <h6 class="mb-0">{{item.name | capitalize }}</h6>
                                </template>
                                <br>
                                <b-card-text>Margin<span class="float-right"> {{item.margin}} $</span></b-card-text>
                                <range-slider class="slider" min="0" max="100" step="1" v-model="item.margin"> </range-slider>

                                <b-card-text>Iron<span class="float-right"> {{item.raw_material.iron}} Units</span></b-card-text>
                                <range-slider class="slider" min="0" max="100" step="1" v-model="item.raw_material.iron"> </range-slider>

                                <b-card-text>Wood <span class="float-right"> {{item.raw_material.wood}} Units</span></b-card-text>
                                <range-slider class="slider" min="0" max="100" step="1" v-model="item.raw_material.wood"> </range-slider>

                                <b-card-text>Glue <span class="float-right"> {{item.raw_material.glue}} Units</span></b-card-text>
                                <range-slider class="slider" min="0" max="100" step="1" v-model="item.raw_material.glue"> </range-slider>

