</template>

<script>
// Importing libs
import axios from 'axios';
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'
// https://github.com/ktsn/vue-range-slider
import RangeSlider from 'vue-range-slider'
// you probably need to import built-in style
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
    data() {
        return {
            items: [{
                    "name": "table",
                    "img": "table.jpg",
                    "margin": 7,
                    raw_material: {
                        "iron": 2,
                        "wood": 1,
                        "glue": 1
                    }
                },
                {
                    "name": "chair",
                    "img": "chair.jpg",
                    "margin": 9,
                    raw_material: {
                        "iron": 4,
                        "wood": 1,
                        "glue": 2
                    }
                },
                {
                    "name": "low_table",
                    "img": "table_basse.jpg",
                    "margin": 18,
                    raw_material: {
                        "iron": 5,
                        "wood": 2,
                        "glue": 3
                    }
                },
                {
                    "name": "stool",
                    "img": "tabouret.jpg",
                    "margin": 17,
                    raw_material: {
                        "iron": 7,
                        "wood": 2,
                        "glue": 3
                    }
                }
            ],
            constraints: [{
                    "name": "iron",
                    "value": 42,
                    "img": "iron.jpg"
                },
                {
                    "name": "wood",
                    "value": 17,
                    "img": "wood.jpg"
                },
                {
                    "name": "glue",
                    "value": 24,
                    "img": "glue.jpg"
                }
            ],
            show: true,
            linearSolverResult: {},
            lpDescription: "Maximize my factory profit under stocks constraints !",
            PulpLpSummary: ""

        }
    },
    components: {
        VueSlider,
        RangeSlider
    },
    computed: {
        total: function () {
            return this.value * 10
        }
    },
    methods: {

        getLinearSolver() {
            const path = '/api/maximize_profit_2';
            var data = {
                "items": this.items,
                "constraints": this.constraints
            }
            axios.post(path, data)
                .then((res) => {

                    this.linearSolverResult = res.data

                    // Designing the PULP Lp desc coming from pulp
                    this.PulpLpSummary = this.linearSolverResult.PulpLpSummary;
                    this.PulpLpSummary.replace(new RegExp('\r?\n', 'g'), '<br />');

                    // Removing the PulpLpSUmmary from the main JSON result
                    delete this.linearSolverResult.PulpLpSummary;
                })
                .catch((error) => {
                    // eslint-disable-next-line
                    console.error(error);
                });
        },
        onSubmit(evt) {
            evt.preventDefault()
            this.getLinearSolver()
        },
        onReset(evt) {
            evt.preventDefault()
            // Reset our form values
            this.form.email = ''
            this.form.name = ''
            this.form.food = null
            this.form.checked = []
            // Trick to reset/clear native browser form validation state
            this.show = false
            this.$nextTick(() => {
                this.show = true
            })
        }
    },
    filters: {
        capitalize: function (value) {
            if (!value) return ''
            value = value.toString()
            return value.charAt(0).toUpperCase() + value.slice(1)
        }
    }
}
</script>

<style>
.main_form {
    margin: 30px;
}

.main_img {
    width: 150px
}

fieldset {
    background-color: #fef5e4 !important;
    border-radius: 4px;
}

legend {
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
    color: var(--purple);
    font-size: 17px;
    font-weight: bold;
    padding: 3px 5px 3px 7px;
    width: auto;
}

.slider {
    /* overwrite slider styles */
    width: 100%;
}
</style>
