<template>
<div class="main-form">
    <div class="row">
        <!-- SIDE  -->
        <div class="col-md-2">
            <img src="../assets/bourse.jpg" class="main_img_big">
            <h5> {{ t('REGISTER') }}  </h5>
        </div>
        <!-- MAIN  -->
        <div class="col-md-10">
            <form class="user" id="form">
                <div class="container">
                    <h1>{{ t('REGISTER') }} </h1>
                    <p>{{ t('FILL_FORM_MESSAGE') }} :</p>
                    <hr>
                    <label for="firstname"><b>{{ t('FIRST_NAME') }}</b></label>
                    <input type="text" class="mr-sm-2 form-control form-control-sm" min="1" v-model="user.firstname" placeholder="Firstname" name="firstname" id="firstname">
                    <!-- ERRORS MESSAGES -->
                    <div v-if="cliqued ">
                        <div class="error" v-if="!$v.user.firstname.required">
                            Field is mandatory
                        </div>
                        <div class="error" v-if="!$v.user.firstname.minLength">
                            First name must be at least {{ $v.user.lastname.$params.minLength.min }} long.
                        </div>
                    </div>
                    <label for="lastname"><b>{{ t('LAST_NAME') }}</b></label>
                    <input type="text" class="mr-sm-2 form-control form-control-sm" min="1" v-model="user.lastname" name="lastname" id="lastname" placeholder="LastName">
                    <!-- ERRORS MESSAGES -->
                    <div v-if="cliqued ">
                        <div class="error" v-if="!$v.user.lastname.required">
                            Field is mandatory
                        </div>
                        <div class="error" v-if="!$v.user.lastname.minLength">
                            First name must be at least
                            {{ $v.user.lastname.$params.minLength.min }} long.
                        </div>
                    </div>
                    <label for="email"><b>{{ t('EMAIL') }}</b></label>
                    <input type="email" class="mr-sm-2 form-control form-control-sm" min="1" v-model="user.email" name="email" id="email" placeholder="Email">
                    <!-- ERRORS MESSAGES -->
                    <div v-if="cliqued ">
                        <div class="error" v-if="!$v.user.email.required">
                            Field is mandatory
                        </div>
                        <div class="error" v-if="!$v.user.email.minLength">
                            First name must be at least
                            {{ $v.user.email.$params.minLength.min }} long.
                        </div>
                        <div class="error" v-if="!$v.user.email.email">
                            Email must be valid
                        </div>
                    </div>
                    <label for="password"><b>{{ t('PASSWORD') }}</b></label>
                    <input type="password" class="mr-sm-2 form-control form-control-sm" min="1" v-model="user.password" name="password" id="password" placeholder="Password">
                    <!-- ERRORS MESSAGES -->
                    <div v-if="cliqued ">
                        <div class="error" v-if="!$v.user.password.required">
                            Field is mandatory
                        </div>
                        <div class="error" v-if="!$v.user.password.minLength">
                            Password must be at least
                            {{ $v.user.password.$params.minLength.min }} long.
                        </div>
                    </div>
                    <hr>
                    <p>{{ t('PRIVACY_MESSAGE') }} <a href="#">{{ t('PRIVACY_TERMS') }}</a>.</p>
                    <button type="button" class="btn my-2 my-sm-0 btn-secondary btn-sm" v-on:click='register'>{{ t('REGISTER') }} </button>&nbsp;
                    <button type="button" class="btn my-2 my-sm-0 btn-secondary btn-sm" v-on:click='resetregister'>{{ t('RESET') }} </button>
                </div>
                <div class="container signin">
                    <p>{{ t('ALREADY_HAVE_AN_ACCOUNT_MESSAGE') }}  <router-link class-active="active" to="/login">{{ t('CONNECT') }}</router-link>.</p>
                </div>
            </form>
        </div>
    </div>
</div>
</template>

<style>
.container {
    padding: 16px;
    background-color: #FEF5E4;
}

hr {
    border: 1px solid grey;
    margin-bottom: 25px;
}

a {
    color: dodgerblue;
}

.signin {
    background-color: #f1f1f1;
    text-align: center;
}

.error {
    color: #FF0000;
}
</style>

<script>
// Importing libs
import axios from 'axios';
import {
    required,
    minLength,
    between,
    email
} from "vuelidate/lib/validators";
// Shared functions
import shared from '../shared'
export default {
    data() {
        return {
            user: {
                firstname: "",
                lastname: "",
                email: "",
                password: ""
            },
            problem: false,
            cliqued: false
        }
    },
    validations: {
        user: {
            lastname: {
                required,
                minLength: minLength(2)
            },
            firstname: {
                required,
                minLength: minLength(2)
            },
            email: {
                required,
                minLength: minLength(2),
                email
            },
            password: {
                required,
                minLength: minLength(2)
            }
        }
    },
    methods: {
        resetregister: function () {
            this.user = {
                lastname: "",
                firstname: "",
                email: "",
                password: ""
            };
            this.problem = false;
            this.cliqued = false;
        },
        register: function () {
            this.cliqued = true;
            //console.log(this.user);
            if (
                !this.user.password ||
                !this.user.firstname ||
                !this.user.lastname ||
                !this.user.email ||
                !this.$v.user.email.email
            ) {
                this.problem = true;
                this.$notify({
                    type: 'error',
                    group: 'foo',
                    title: 'Hey! ',
                    text: 'Something is wrong, please check the fields again !'
                });
                return;
            }
            // Server call
            axios
                .post("/registerUser", this.user)
                .then(response => {
                    if (response.data.problem) {
                        alert(response.data.problem);
                        console.log(response.data.problem);
                    } else {
                        this.$notify({
                            type: 'success',
                            group: 'foo',
                            title: 'Hey! ',
                            text: 'Account created !'
                        });
                    }
                })
                .catch(error => {
                    console.log(error);
                    this.$notify({
                        type: 'error',
                        group: 'foo',
                        title: 'Hey! ',
                        text: error
                    });
                });
        }
    }
}
</script>
